<template>
  <div class="page">
    <div class="container">
      <div class="turn-card">
        <mk-turn-card
          ref="turnCard"
          v-bind="baseConfig"
          @stateChange="stateChange"
          @clickStart="clickStart"
        ></mk-turn-card>
        <div class="info">
          <p>当前状态：{{ state }}</p>
        </div>
      </div>
    </div>
    <demo-block card title="基础操作">
      <button class="common-button primary" @click="reset">全部重置</button>
      <button class="common-button primary" @click="disable">禁用</button>
    </demo-block>
  </div>
</template>
<script>
export default {
  data() {
    return {
      state: 0,
      baseConfig: Object.freeze({
        cardsConfig: {
          img:
            '//yun.tuisnake.com/h5-mani/turncard_fivelucky/9722f608-dfb4-4b52-bfb3-880406c637a0.png',
          length: 9,
        },
        frontImg:
          '//yun.tuisnake.com/h5-mani/turncard_fivelucky/f25455fe-ce70-431f-8086-c5fe2d7e4dfc.png',
      }),
    };
  },

  methods: {
    stateChange(opt) {
      this.state = opt.now;
    },
    async clickStart(index) {
      const turnCard = this.$refs.turnCard;
      if (!turnCard.isWaitStart) {
        console.log('当前不可开始');
        return;
      }
      // 校验抽奖次数
      if (turnCard.getCardsState().filter((it) => !it).length <= 2) {
        console.log('抽奖次数已用完');
        return;
      }
      console.log(`您抽中的卡牌是第${index + 1}张`);
      // 翻转对应索引卡牌
      await turnCard.start({
        index,
        frontImg: this.baseConfig.frontImg,
      });
      console.log('翻转动画结束');
      await turnCard.reset();
    },
    async reset() {
      await this.$refs.turnCard.reset({
        needResetAll: true,
      });
    },
    disable() {
      this.$refs.turnCard.disable();
    },
  },
};
</script>
